  font-family: "Open Sans";
  src: url("../fonts/OpenSans-ExtraBold.woff2") format("woff2"), url("../fonts/OpenSans-ExtraBold.woff") format("woff");
  font-weight: 800;
  font-style: normal;
  font-display: swap;
}

body {
  overflow-x: hidden;
  font-family: "Open Sans", sans-serif;
  color: var(--color-black);
}

img {
  max-width: 100%;
  height: auto;
}

a {
  display: inline-block;
  text-decoration: none;
  color: inherit;
}

button {
  padding: 0;
  border: none;
  background: none;
  cursor: pointer;
}

.wrapper {
  max-width: 1600px;
  margin: 0 auto;
  padding-left: 80px;
  padding-right: 80px;
}

.body {
  background: var(--color-white) url(../img/body-bg.svg) no-repeat center/contain;
}

.body--interpretation {
  padding-bottom: 130px;
}

.body--element {
  padding: 40px;
}

.h1,
.h2,
.h3 {
  text-transform: uppercase;
  color: var(--color-blue);
}

.h2,
.h3 {
  font-weight: 700;
}

.h1 {
  font-weight: 800;
  font-size: 40px;
  line-height: 54px;
}

.h2 {
  font-size: 30px;
  line-height: 40px;
}

.h3 {
  font-size: 25px;
  line-height: 34px;
}

.p {
  font-size: 25px;
  line-height: 34px;
  font-weight: 400;
}

table {
  width: 100%;
  background-color: var(--color-white);
  border-radius: 10px;
  box-shadow: 1px 1px 5px 1px rgba(0, 0, 0, 0.25);
}

table thead tr {
  background-color: var(--color-green);
  color: var(--color-white);
  font-weight: 600;
  font-size: 22px;
  line-height: 29px;
  border-bottom: none;
  border-radius: 10px;
}

table thead tr td:first-child {
  border-radius: 10px 0 0 10px;
}

table thead tr td:last-child {
  border-radius: 0 10px 10px 0;
}

table tbody tr {
  font-size: 20px;
  line-height: 27px;
  border-bottom: 1px solid var(--color-black);
}

table tbody tr:last-child {
  border-bottom: none;
}

table td {
  padding: 18px 24px;
}

.item {
  padding: 12px 20px;
  font-size: 18px;
  line-height: 24px;
  font-weight: 600;
  border: 2px solid var(--color-green);
  border-radius: 10px;
  transition: color 0.5s ease, background 0.5s ease;
}

.item:hover,
.item--active {
  color: var(--color-white);
  background-color: var(--color-green);
}

.button {
  display: inline-block;
  padding: 18px 36px 18px 86px;
  font-size: 22px;
  line-height: 28px;
  font-weight: 700;
  color: var(--color-blue);
  border: 5px solid var(--color-green);
  border-radius: 50px;
  position: relative;
